.common-side-menu::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 6px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
.common-side-menu::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #C6D4E7;
  border-radius: 3px;
}
.common-side-menu::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  border-radius: 10px;
  background: #E3E8EF;
}
.common-side-menu {
  position: absolute;
  overflow: hidden;
  height: calc(100vh - 48px);
  overflow-y: auto;
  background: #ECEFF3;
}
.common-side-menu [class^="icon-"] {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  vertical-align: middle;
}
.common-side-menu [class^="icon-"]:before {
  content: '';
}
