.dss-dialog-wrapper .el-dialog__header {
  margin: 22px 15px 0;
  padding-left: 0;
  border-bottom: 1px solid #d7e0ea;
}
.dss-dialog-wrapper .el-dialog__title {
  margin-left: 30px;
  font-size: 14px;
}
.dss-dialog-wrapper .el-dialog__body {
  padding: 30px 45px;
}
.dss-dialog-wrapper .el-dialog__body .el-dialog__footer .el-button {
  height: 30px;
  width: 106px;
  font-size: 12px;
}
.dss-popover {
  box-sizing: border-box;
  padding: 32px;
}
.dss-popover .el-popover__title {
  margin-bottom: 0;
}
.dss-popover .footer-btn-container {
  text-align: center;
  margin-top: 20px;
}
.dss-popover .dss-popover-title {
  margin-bottom: 6px;
}
.dss-popover .dss-popover-title .title-icon {
  width: 30px;
  height: 30px;
}
.dss-popover .dss-popover-title .title-text {
  display: inline-block;
  vertical-align: top;
  margin-left: 8px;
  line-height: 30px;
  height: 30px;
  font-size: 14px;
  color: #31343B;
}
.dss-popover .content-text {
  font-size: 12px;
  font-family: MicrosoftYaHei;
  color: #31343b;
  line-height: 24px;
  height: 24px;
}
