body {
  height: calc(100vh);
  overflow: hidden;
  overflow-x: auto;
}
.wkb-group-table {
  /***微信头像******************/
  /**商品图片和名称***********/
}
.wkb-group-table .table-header .el-col {
  padding: 15px 0;
}
.wkb-group-table .table-row {
  border: 1px solid #DCDEE0;
  margin-bottom: 10px;
  border-radius: 4px;
  background: white;
}
.wkb-group-table .table-row .td-header {
  background: #f8f9fe;
  border-radius: 4px;
  font-size: 12px;
  color: #333333;
}
.wkb-group-table .table-row .td-body {
  padding: 20px 0;
}
.wkb-group-table .table-row .td-header div:first-child.el-col,
.wkb-group-table .table-row .td-body div:first-child.el-col {
  padding-left: 15px;
}
.wkb-group-table .table-row .td-header .el-col,
.wkb-group-table .table-row .td-body .el-col {
  padding: 8px 0;
}
.wkb-group-table .leader-avatar {
  width: 22px;
  height: 22px;
  vertical-align: middle;
  display: inline-block;
  padding-right: 7px;
  margin-top: -3px;
}
.wkb-group-table .img-info {
  display: flex;
  align-items: center;
}
.wkb-group-table .img-info .info {
  padding-left: 5px;
}
.wkb-group-table .img-info .name {
  display: inline-block;
  color: #31343b;
  font-weight: bold;
}
.wkb-group-table .img-info .img-box {
  display: flex;
  align-items: center;
  width: 79px;
  height: 79px;
  box-sizing: border-box;
  line-height: 79px;
  border: 1px solid #e4e7ed;
}
.wkb-group-table .img-info .img-box img {
  margin: 0 auto;
  max-width: 69px;
  max-height: 69px;
}
