.dsp-ellipsis {
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
}
.dsp-pointer {
  cursor: pointer;
}
.dsp-push-list {
  padding: 20px;
}
.dsp-filter {
  display: flex;
}
.dsp-filter .right {
  margin-left: auto;
}
.dsp-filter .el-form--inline .el-form-item {
  margin-right: 20px;
}
.dsp-filter .el-button {
  padding: 10px 20px;
}
.dsp-filter .el-button i {
  font-size: 15px;
}
.dsp-filter .el-button.min {
  background: #99aaf9;
  border-color: #99aaf9;
}
.dsp-filter .filter-left-date {
  height: 36px;
  background: transparent;
  border: 2px solid rgba(1, 35, 52, 0.06);
  color: rgba(1, 35, 52, 0.5);
  border-radius: 2px;
}
.dsp-filter .filter-left-date .el-range-input {
  background: none;
}
.dsp-filter .el-range-editor--small.el-input__inner {
  line-height: 40px;
  border: 2px solid rgba(1, 35, 52, 0.1);
  margin-right: 15px;
  background: none;
}
.dsp-filter .el-range-editor--small.el-input__inner .el-range-input {
  background: none;
}
.dsp-filter .el-range-editor--small .el-range-separator {
  line-height: 30px;
}
.dsp-filter .el-form-item__content > .el-select .el-input .el-input__inner {
  background: transparent;
  border: 2px solid rgba(1, 35, 52, 0.1);
  color: #012334;
  font-size: 14px;
}
.dsp-filter .el-form-item__content > .el-select .el-input .el-input__inner:focus {
  border: 2px solid rgba(1, 35, 52, 0.2);
}
.dsp-filter .el-form-item__content > .el-input {
  background-color: none;
  box-shadow: none;
}
.dsp-filter .el-form-item__content > .el-input .el-input__inner {
  background: transparent;
  border: none;
  border-bottom: 2px solid rgba(1, 35, 52, 0.1);
  border-radius: 0;
  color: #012334;
  font-size: 14px;
}
.dsp-filter .el-form-item__content > .el-input .el-input__inner:-ms-input-placeholder {
  color: rgba(1, 35, 52, 0.5);
}
.dsp-filter .el-form-item__content > .el-input .el-input__inner::-ms-input-placeholder {
  color: rgba(1, 35, 52, 0.5);
}
.dsp-filter .el-form-item__content > .el-input .el-input__inner::placeholder {
  color: rgba(1, 35, 52, 0.5);
}
.dsp-filter .el-form-item__content > .el-input .el-input__inner:focus {
  border-color: rgba(1, 35, 52, 0.2);
  color: #012334;
}
.dsp-icon.mt13-ml13 {
  margin: 13px 0 0 13px;
}
.dsp-new {
  margin-top: 30px;
  padding: 60px 9%;
  background: #fff;
  box-shadow: 0 2px 8px 0 rgba(1, 35, 52, 0.05);
  border-radius: 2px;
}
.dsp-default-add-btn {
  border: 1px solid rgba(91, 118, 247, 0.5);
  border-radius: 2px;
  padding: 7px 15px;
  background: #fff;
  margin: 5px 0;
  color: #5B76F7;
  cursor: pointer;
}
.mb50 {
  margin-bottom: 50px;
}
.mr3 {
  margin-right: 3px;
}
.color-red {
  color: #fc6042;
}
.color-yellow {
  color: #ec9904;
}
.color-green {
  color: #3fba02;
}
.color-default {
  color: #5b76f7;
}
.tips {
  float: left;
  width: 100%;
  margin: 5px -5px;
}
.tips-ts {
  float: left;
  width: 100%;
  margin: 10px 0 0;
}
.tips-ts span {
  opacity: 0.5;
}
.tips-ts b {
  font-size: 14px;
  letter-spacing: 0.02px;
  margin: 0 12px;
}
.dsp-echart-tooltip {
  background: transparent;
  border-radius: 2px;
  color: #fff;
  font-weight: 600;
}
.dsp-echart-tooltip p {
  margin-bottom: 4px;
}
.dsp-echart-tooltip em {
  margin-left: 5px;
  font-style: normal;
}
.dsp-echart-tooltip .title {
  letter-spacing: normal;
  font-weight: 600;
}
.dsp-echart-tooltip .content > span {
  color: rgba(255, 255, 255, 0.8);
}
.dsp-echart-tooltip .icon-circle {
  display: inline-block;
  margin-right: 4px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
