@font-face {
  font-family: 'Montserrat';
  src: url('https://cdn.wakecloud.com/resources/dss-web-portal/fonts/Montserrat-Regular.ttf') format("truetype");
}
@font-face {
  font-family: 'Montserrat';
  src: url('https://cdn.wakecloud.com/resources/dss-web-portal/fonts/Montserrat-Bold.ttf') format("truetype");
  font-weight: bold;
}
body {
  background: #F1F2F8;
  font-family: "Montserrat", "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial;
}
body .el-table th.gutter {
  display: table-cell !important;
}
body .el-table .el-button {
  min-width: 0;
}
.app .el-main {
  box-sizing: border-box;
  padding: 40px 40px 40px 180px;
}
