.key-transparent {
  margin: 0 auto;
  text-align: center;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Arial;
  background-color: #ffffff;
}
p {
  margin: 0 auto;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
.el-loading-mask {
  z-index: 9998 !important;
}
.el-message {
  z-index: 10000 !important;
}
.dss-common-msg {
  z-index: 10000 !important;
}
@media screen and (max-width: 770px) {
  body {
    min-width: auto !important;
  }
  .pc {
    display: none !important;
  }
  .mobile {
    display: block !important;
  }
  .key-transparent {
    padding-top: 2.24rem !important;
  }
}
body {
  min-width: 1000px;
}
.header-nav .pc {
  position: relative;
  top: 0px;
  padding: 0;
  min-width: 1000px;
  z-index: 9999;
}
.header-nav .pc .header {
  height: 60px;
  padding: 0 30px;
  text-align: right;
  background-color: transparent;
}
.header-nav .pc .header .logo {
  position: absolute;
  top: 17px;
  left: 30px;
  color: #ffffff;
  font-size: 14px;
  outline: none;
  transition: color 0.4s;
  cursor: pointer;
}
.header-nav .pc .header .header-logo {
  width: 160px;
  outline: none;
  border: none;
}
.header-nav .pc .header .privateLogo {
  display: none;
}
.header-nav .pc .header .header-menu {
  position: absolute;
  top: 0;
  left: 220px;
  display: inline-block;
  text-align: left;
}
.header-nav .pc .header .header-menu a {
  display: inline-block;
  width: auto;
  position: relative;
  margin: 0 15px;
  line-height: 60px;
  height: 60px;
  color: #ffffff;
  font-size: 14px;
  opacity: 0.5;
}
.header-nav .pc .header .header-menu a:hover {
  opacity: 1;
}
.header-nav .pc .header .index .pchm-index {
  opacity: 1;
}
.header-nav .pc .header .wkb .pchm-wkb {
  opacity: 1;
}
.header-nav .pc .header .klb .pchm-klb {
  opacity: 1;
}
.header-nav .pc .header .da .pchm-da {
  opacity: 1;
}
.header-nav .pc .header .dmp .pchm-dmp {
  opacity: 1;
}
.header-nav .pc .header .aboutUS .pchm-aboutUS {
  opacity: 1;
}
.header-nav .pc .header .btn-box {
  display: inline-block;
  height: 60px;
  line-height: 60px;
}
.header-nav .pc .header .btn-box a {
  display: inline-block;
  vertical-align: middle;
  width: 100px;
  text-align: center;
  line-height: 30px;
  box-sizing: border-box;
  border-radius: 4px;
  color: #fff;
  font-size: 14px;
}
.header-nav .pc .header .btn-box .onTrial {
  line-height: 28px;
  border: 1px solid #ffffff;
  border-radius: 15px;
  margin-right: 10px;
}
.header-nav .pc .header .btn-box .login {
  background: #517cf6;
  border-radius: 15px;
}
.header-nav .pc .header .avatar-box {
  display: inline-block;
  vertical-align: middle;
  margin-left: 25px;
  height: 60px;
  position: relative;
  width: auto !important;
  opacity: 1;
  transition: opacity 0.4s ease;
}
.header-nav .pc .header .avatar-box img {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
}
.header-nav .pc .header .avatar-box .avatar-blue {
  display: none;
}
.header-nav .pc .header .avatar-box .user-info {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
  color: #fff;
  font-size: 16px;
  height: 60px;
  line-height: 60px;
}
.header-nav .pc .header .avatar-box .avatar-ul {
  position: absolute;
  padding: 12px 0;
  width: 114px;
  left: 50%;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0 7px 47px 0 rgba(148, 148, 148, 0.21);
  z-index: 9999;
  transform: scaleY(0) translateX(-50%);
  -ms-transform: scaleY(0) translateX(-50%);
  -moz-transform: scaleY(0) translateX(-50%);
  -webkit-transform: scaleY(0) translateX(-50%);
}
.header-nav .pc .header .avatar-box .avatar-ul a {
  display: block;
  width: 80%;
  text-align: center;
  padding: 5px 0;
  color: #333333;
  font-size: 16px;
  margin: 0 auto;
  border-radius: 4px;
}
.header-nav .pc .header .avatar-box .avatar-ul a:hover {
  color: #3268D5 !important;
}
.header-nav .pc .header .avatar-box:hover .avatar-ul {
  transform: scaleY(1) translateX(-50%);
  -ms-transform: scaleY(1) translateX(-50%);
  -moz-transform: scaleY(1) translateX(-50%);
  -webkit-transform: scaleY(1) translateX(-50%);
}
.header-nav .pc .privateHeader {
  background-color: #ffffff;
}
.header-nav .pc .privateHeader .publicLogo {
  display: none;
}
.header-nav .pc .privateHeader .privateLogo {
  display: block;
}
.header-nav .pc .privateHeader .header-menu a {
  color: #4A536C !important;
}
.header-nav .pc .privateHeader .header-menu a:hover {
  opacity: 1;
}
.header-nav .pc .privateHeader .btn-box .onTrial {
  color: #2A67E3 !important;
  border: 1px solid #2A67E3 !important;
}
.header-nav .pc .privateHeader .avatar-box .avatar {
  display: none;
}
.header-nav .pc .privateHeader .avatar-box .avatar-blue {
  display: inline-block;
}
.header-nav .pc .privateHeader .avatar-box .user-info {
  color: #4A536C;
}
.header-nav .mobile {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 2.24rem;
  background-color: #ffffff;
  z-index: 9999;
  display: none;
}
.header-nav .mobile .header {
  display: flex;
  align-items: center;
  padding: 0 0.53333333rem;
  font-size: 0;
  z-index: 10;
}
.header-nav .mobile .header .left {
  display: inline-block;
  text-align: left;
}
.header-nav .mobile .header .left .header-logo {
  width: 5.94666667rem;
  height: 1.01333333rem;
  padding: 0.61333333rem 0;
}
.header-nav .mobile .header .right {
  flex: 1;
  text-align: right;
}
.header-nav .mobile .header .right .avatar-box {
  display: inline-block;
  position: relative;
  width: 1.49333333rem;
  height: 1.49333333rem;
  margin: 0.37333333rem 0;
}
.header-nav .mobile .header .right .avatar-box .avata-unLogin .avatar {
  width: 100%;
  height: 100%;
  background-color: #EDF1F7;
  border-radius: 50%;
}
.header-nav .mobile .header .right .avatar-box .avata-login {
  display: none;
}
.header-nav .mobile .header .right .avatar-box .avata-login input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  border: 0;
  z-index: 1;
  cursor: pointer;
  opacity: 0;
}
.header-nav .mobile .header .right .avatar-box .avata-login .avatar {
  width: 100%;
  height: 100%;
  background-color: #EDF1F7;
  border-radius: 50%;
}
.header-nav .mobile .header .right .key-header-more {
  display: inline-block;
  position: relative;
  width: 1.49333333rem;
  height: 1.49333333rem;
  margin: 0.37333333rem 0 0.37333333rem 1.06666667rem;
  vertical-align: top;
}
.header-nav .mobile .header .right .key-header-more input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  border: 0;
  z-index: 1;
  cursor: pointer;
  opacity: 0;
}
.header-nav .mobile .header .right .key-header-more .more {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #EDF1F7;
  border-radius: 50%;
  opacity: 1;
  transition: opacity 0.5s ease;
}
.header-nav .mobile .header .right .key-header-more .close {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #EDF1F7;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.5s ease;
}
.header-nav .mobile .header-avatar {
  position: fixed;
  top: 2.24rem;
  bottom: 0;
  right: -100%;
  width: 14.93333333rem;
  text-align: center;
  opacity: 0;
  transition: all 0.5s ease;
  z-index: 9;
}
.header-nav .mobile .header-avatar .content {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 13.33333333rem;
  padding: 0.4rem 0.8rem;
  background: #f4f4f4;
  z-index: 10;
}
.header-nav .mobile .header-avatar .content .first-menu-ul {
  position: relative;
  padding: 0 0.26666667rem;
}
.header-nav .mobile .header-avatar .content .first-menu-ul .first-menu-li p {
  font-size: 0.8rem;
  font-weight: 400;
  color: #464D6B;
  line-height: 42px;
  text-align: left;
}
.header-nav .mobile .header-avatar .content .first-menu-ul .first-menu-li p a {
  display: block;
  color: #464D6B;
}
.header-nav .mobile .header-menu {
  position: fixed;
  top: 2.24rem;
  bottom: 0;
  right: -100%;
  width: 14.93333333rem;
  text-align: center;
  opacity: 0;
  transition: all 0.5s ease;
  z-index: 9999;
}
.header-nav .mobile .header-menu .content {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 13.33333333rem;
  padding: 0.4rem 0.8rem;
  background: #f4f4f4;
  z-index: 10;
}
.header-nav .mobile .header-menu .content .first-menu-ul {
  position: relative;
  padding: 0 0.26666667rem;
}
.header-nav .mobile .header-menu .content .first-menu-ul p {
  font-size: 0.8rem;
  font-weight: 400;
  color: #464D6B;
  line-height: 42px;
  text-align: left;
}
.header-nav .mobile .header-menu .content .first-menu-ul p a {
  display: block;
  color: #464D6B;
}
.header-nav .mobile .header-menu .content .btn-box {
  font-size: 0;
  margin-top: 1.86666667rem;
}
.header-nav .mobile .header-menu .content .btn-box span {
  display: inline-block;
  width: 6.26666667rem;
  height: 2.13333333rem;
  line-height: 2.13333333rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 400;
  border: 1px solid #3268D6;
}
.header-nav .mobile .header-menu .content .btn-box .btn-login {
  background-color: #3268D6;
}
.header-nav .mobile .header-menu .content .btn-box .btn-login a {
  display: block;
  color: #ffffff;
}
.header-nav .mobile .header-menu .content .btn-box .btn-register {
  margin-left: 0.53333333rem;
}
.header-nav .mobile .header-menu .content .btn-box .btn-register a {
  display: block;
  color: #3268D6;
}
.header-nav .mobile-more-on .header .right .key-header-more .more {
  opacity: 0;
}
.header-nav .mobile-more-on .header .right .key-header-more .close {
  opacity: 1;
}
.header-nav .mobile-more-on .header-menu {
  opacity: 1;
  right: 0rem;
}
.header-nav .mobile-more-on .header-menu::after {
  content: "";
  position: fixed;
  top: 2.24rem;
  left: 0rem;
  right: 0rem;
  bottom: 0rem;
  background: rgba(255, 255, 255, 0.5);
}
.header-nav .mobile-avatar-on .header-avatar {
  opacity: 1;
  right: 0rem;
}
.header-nav .mobile-avatar-on .header-avatar::after {
  content: "";
  position: fixed;
  top: 2.24rem;
  left: 0rem;
  right: 0rem;
  bottom: 0rem;
  background: rgba(255, 255, 255, 0.5);
}
.header-nav .app-loading {
  text-align: center;
  padding: 100px;
}
.header-nav .app-loading-hide {
  display: none;
}
